<template>
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">
        <i class="fas fa-chart-pie mr-1"></i>
        {{ $t("tiposResoluciones") }}
      </h3>
    </div>
    <!-- /.card-header -->
    <div class="card-body">
      <div class="chart">
        <DoughnutChart
          style="position: relative; height: 300px;"
          :chart-data="datacollection"
        ></DoughnutChart>
      </div>
    </div>
    <!-- /.card-body -->
  </div>
</template>

<script>
import DoughnutChart from "@/components/graficos/DoughnutChart.js";

import { chartColors, dynamicColors } from "@/util/graficos";
import { mapState, mapGetters } from "vuex";

function backgroundColor(label) {
  let color;
  switch (label.split(" ")[0]) {
    case "faltas":
      color = chartColors.blue;
      break;

    case "penal":
      color = chartColors.red;
      break;

    case "contravencional":
      color = chartColors.yellow;
      break;

    case "amparo":
      color = chartColors.green;
      break;

    case "allanamiento_autonomo":
      color = chartColors.orange;
      break;

    default:
      color = dynamicColors();
      break;
  }

  return color;
}

export default {
  components: {
    DoughnutChart,
  },
  data() {
    return {
      datacollection: {},
    };
  },
  mounted() {
    this.fillData();
  },
  computed: {
    ...mapState(["resolucionActual"]),
    ...mapGetters(["getTiposLabel", "getTiposValues"]),
  },
  watch: {
    resolucionActual() {
      this.fillData();
    },
  },
  methods: {
    fillData() {
      this.datacollection = {
        labels: this.getTiposLabel,
        datasets: [
          {
            data: this.getTiposValues,
            backgroundColor: this.getTiposLabel?.map((x) => backgroundColor(x)),
            //["red", chartColors.orange, chartColors.yellow]
          },
        ],
      };
    },
  },
};
</script>

<i18n>
{
  "es": {
    "tiposResoluciones" : "Tipos de Resoluciones"
  },
  "en": {
    "tiposResoluciones" : "Topic of Decisions"
  }
}
</i18n>
