<template>
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">
        <i class="fas fa-chart-bar mr-1"></i>
        Ranking de Articulos más infringidos (Top 5)
      </h3>
    </div>
    <!-- /.card-header -->
    <div class="card-body">
      <div class="chart">
        <HorizontalBarChart
          style="position: relative; height: 300px;"
          :chart-data="datacollection"
        ></HorizontalBarChart>
      </div>
    </div>
    <!-- /.card-body -->
  </div>
</template>

<script>
import HorizontalBarChart from "@/components/graficos/HorizontalBarChart.js";
import { mapGetters } from "vuex";

export default {
  components: {
    HorizontalBarChart
  },
  data() {
    return {
      datacollection: {}
    };
  },
  mounted() {
    this.fillData();
  },
  computed: {
    ...mapGetters(["getRankingArticulos"])
  },
  watch: {
    getRankingArticulos() {
      this.fillData();
    }
  },
  methods: {
    fillData() {
      this.datacollection = {
        labels: this.getRankingArticulos.map(x => x.articulo),
        datasets: [
          {
            data: this.getRankingArticulos.map(x => x.cant),
            backgroundColor: [
              "rgba(255, 99, 132, 0.4)",
              "rgba(255, 159, 64, 0.4)",
              //"rgba(255, 205, 86, 0.2)",
              "rgba(75, 192, 192, 0.4)",
              "rgba(54, 162, 235, 0.4)",
              "rgba(153, 102, 255, 0.4)",
              "rgba(201, 203, 207, 0.4)"
            ],
            borderColor: [
              "rgb(255, 99, 132)",
              "rgb(255, 159, 64)",
              //"rgb(255, 205, 86)",
              "rgb(75, 192, 192)",
              "rgb(54, 162, 235)",
              "rgb(153, 102, 255)",
              "rgb(201, 203, 207)"
            ],
            fill: false,
            borderWidth: 2
          }
        ]
      };
    }
  }
};
</script>

